  from {
    color: red;
  }
  to {
    color: none;
  }
}

:root {
  line-height: 1.5;
}

html {
  font-family: sans-serif;
}

body {
  background-color: rgb(17, 17, 17);
  color: antiquewhite;
  height: 60%;
  margin-top: 50px;
  min-width: 865px;
}

h1,
h2,
h3,
h4,
h5,
figure,
p,
ol,
ul {
  margin: 0;
  text-align: center;
}

.info {
  display: flex;
  justify-content: center;
  height: 30px;
  padding-bottom: 10px;
  min-width: 865px;
}

.board-labels {
  display: grid;
  grid-template-columns: repeat(2, 330px);
  place-items: center;
  justify-content: center;
  gap: 180px;
  padding-bottom: 10px;
  min-width: 865px;
}

.gameboards {
  display: grid;
  grid-template-columns: 1fr 180px 1fr;
  place-self: center;
  place-items: center;
  max-width: fit-content;
  margin: 0 auto;
  position: relative;
}

.primary-board-container,
.secondary-board-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.primary-board,
.secondary-board {
  display: grid;
  grid-template-rows: repeat(11, 30px);
  grid-template-columns: repeat(11, 30px);
  gap: 1px;
  background-color: black;
  border: 1px solid black;
  width: fit-content;
  margin: 0;
}

.grid-label-row,
.grid-label-col {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: small;
  color: grey;
  background-color: rgb(56, 56, 56);
}

.primarySquare,
.secondarySquare {
  height: 30px;
  width: 30px;
  border: none;
  background-color: rgb(84, 94, 237);
}

.primaryShipIcons,
.secondaryShipIcons {
  display: grid;
  grid-template-rows: 15px;
  grid-template-columns: repeat(21, 15px);
  gap: 1px;
  background-color: black;
  border: 1px solid black;
  width: fit-content;
  margin: 0;
  padding-top: 17px;
}

.ship {
  background-color: grey;
}

.hit {
  background-color: red;
}

.miss {
  background-color: antiquewhite;
}

.shipIcon {
  border: none;
  background-color: grey;
}

.shipIconHit {
  border: none;
  background-color: red;
}

.emptyIcon {
  background-color: inherit;
  border: none;
}

.shipStartH {
  border-top: 1px solid rgb(56, 56, 56);
  border-left: 1px solid rgb(56, 56, 56);
  border-bottom: 1px solid rgb(56, 56, 56);
}

.shipStartV {
  border-top: 1px solid rgb(56, 56, 56);
  border-left: 1px solid rgb(56, 56, 56);
  border-right: 1px solid rgb(56, 56, 56);
}

.shipMidH {
  border-top: 1px solid rgb(56, 56, 56);
  border-bottom: 1px solid rgb(56, 56, 56);
}

.shipMidV {
  border-left: 1px solid rgb(56, 56, 56);
  border-right: 1px solid rgb(56, 56, 56);
}

.shipEndH {
  border-top: 1px solid rgb(56, 56, 56);
  border-right: 1px solid rgb(56, 56, 56);
  border-bottom: 1px solid rgb(56, 56, 56);
}

.shipEndV {
  border-left: 1px solid rgb(56, 56, 56);
  border-right: 1px solid rgb(56, 56, 56);
  border-bottom: 1px solid rgb(56, 56, 56);
}

.gameStart {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  gap: 20px;
}

.startGameButton {
  width: fit-content;
}

.gameOver {
  display: none;
  flex-direction: column;
  position: absolute;
  align-items: center;
  justify-content: center;
  top: 70px;
  left: 235px;
  height: 200px;
  width: 420px;
}

.gameOverText {
  color: white;
  font-size: xx-large;
  padding-bottom: 10%;
}

.sunk-ship {
  animation-name: sunk;
  animation-duration: 1s;
}

.fade {
  opacity: 50%;
}

.win {
  border: 2px solid green;
  background-color: rgb(1, 105, 1);
}

.loss {
  border: 2px solid red;
  background-color: rgb(131, 2, 2);
}

.playAgain {
  display: block;
}
